body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  border: 2px solid blue;
  background: black;
  color: white;
}

.lightOn {
  background: red;
  border: 2px solid blue;
  border-radius: 50%;
  font-size: 10vmin;
  text-align: center;
  width: 15vmin;
}

.lightOff {
  background: grey;
  border: 2px solid blue;
  border-radius: 50%;
  font-size: 10vmin;
  text-align: center;
  width: 15vmin;
}

.congratSpin {
    -webkit-animation-name: spinIcon;
            animation-name: spinIcon;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    max-width: 30vmin;
    max-height: 30vmin;
    position: fixed;
    display: block;
    background: black;
}

@-webkit-keyframes spinIcon {
    0% {top: 0%; left : 0%; -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    25% {top: 50%; left : 10%; -webkit-transform: rotate(90deg); transform: rotate(90deg) }
    50% {top: 50%; left : 65%; -webkit-transform: rotate(180deg); transform: rotate(180deg) }
    100% {top: 0%; left: 100%; -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

@keyframes spinIcon {
    0% {top: 0%; left : 0%; -webkit-transform: rotate(0deg); transform: rotate(0deg) }
    25% {top: 50%; left : 10%; -webkit-transform: rotate(90deg); transform: rotate(90deg) }
    50% {top: 50%; left : 65%; -webkit-transform: rotate(180deg); transform: rotate(180deg) }
    100% {top: 0%; left: 100%; -webkit-transform: rotate(360deg); transform: rotate(360deg) }
}

.colourChange {
    -webkit-animation-name: RGBText;
            animation-name: RGBText;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    font-size: 30px;
}

@-webkit-keyframes RGBText {
    0%   {color : red}
    25%  {color: yellow}
    50%  {color: green}
    75%  {color: blue}
    100%  {color: blueviolet}
}

@keyframes RGBText {
    0%   {color : red}
    25%  {color: yellow}
    50%  {color: green}
    75%  {color: blue}
    100%  {color: blueviolet}
}
